* {
  box-sizing: border-box;
}

body {
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', Roboto, Oxygen,
    Ubuntu, Cantarell, 'Open Sans', 'Helvetica Neue', sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  background-color: #fafafa;
  color: #212121;
  line-height: 1.5;
}

#search-box {
  font: inherit;
  padding: 8px;
  max-width: 320px;
  margin-bottom: 16px;
}

.country-list{
  list-style-type: none;
  padding-left: 0;
  margin-top: 0;

}

.search-form {
  display: flex;
  justify-content: center;
  padding-top: 5px;
  padding-bottom: 5px;
  background-color: aqua;
}

.load-more {
  display: block;
  margin-top: 15px;
  margin-left: auto;
  margin-right: auto;

}

.gallery {
  display: flex;
  justify-content: space-evenly;
  flex-wrap: wrap;
}

.info {
  display: flex;
  justify-content: space-around;
  box-shadow: 0px 4px 4px rgba(184, 184, 184, 0.459);
  border-radius: 5px;
  
}

.img {
  display: block;
}

.info-item {
  display: flex;
  flex-direction: column;
  align-items: center;
  
}